import TableWrapper from "./table-wrapper";

declare let AdminColumns: any;

export default class ScrollIndicator {

    table: HTMLTableElement
    wrapper: TableWrapper
    initialX: number
    xOffset: number
    maxX: number
    tempX: number
    active: boolean
    element: HTMLElement
    dragger: HTMLElement
    timeout: any
    percentage: number

    constructor(table: HTMLTableElement, wrapper: TableWrapper) {
        this.table = table;
        this.wrapper = wrapper;

        this.initialX = 0;
        this.xOffset = 0;
        this.maxX = 0;
        this.tempX = 0;
        this.active = false;

        this.create();
        this.updateDraggerWidth();
        this.updateWidth();
    }

    disable() {
        this.element.style.display = 'none';
    }

    enable() {
        this.element.style.display = 'block';
    }

    hide() {
        this.element.classList.add('-hidden');
    }

    show() {
        this.element.classList.remove('-hidden');
    }

    create() {
        let element = document.createElement('div');
        let dragger = document.createElement('div');

        element.classList.add('acp-scrolling-indicator');
        element.classList.add('-start');
        element.classList.add('-hidden');
        dragger.classList.add('acp-scrolling-indicator__dragger');

        element.appendChild(dragger);
        document.body.appendChild(element);

        this.element = element;
        this.dragger = dragger;

        setTimeout(() => {
            this.maxX = element.clientWidth - dragger.offsetWidth;
        });

        this.initEvents();
        this.disable();
        this.updateYPosition();
    }

    updateWidth() {
        this.element.style.width = this.table.offsetWidth + 'px';
    }

    updateYPosition() {
        let tableBottom = this.table.getBoundingClientRect().bottom;
        let bottom = window.innerHeight - tableBottom;

        if (bottom > this.element.offsetHeight) {
            this.element.style.top = (window.innerHeight - bottom) + 'px';
        } else {
            this.element.style.top = 'inherit';
        }
    }

    updateDraggerWidth() {
        let percentage = Math.round((this.table.offsetWidth / this.table.scrollWidth * 100));

        if (percentage === 100) {
            this.element.classList.add('-fits');
        } else {
            this.element.classList.remove('-fits');
        }

        this.dragger.style.width = `${percentage}%`;
        this.maxX = this.element.clientWidth - this.dragger.offsetWidth;
    }

    initEvents() {
        document.addEventListener('scroll', () => {
            this.updateYPosition();
            this.updateWidth();
        });

        window.addEventListener("touchmove", e => this.drag(e), false);
        window.addEventListener("mousemove", e => this.drag(e), false);
        window.addEventListener('mouseup', e => this.dragEnd());

        this.dragger.addEventListener("touchstart", e => this.dragStart(e));
        this.dragger.addEventListener("touchend", () => this.dragEnd());

        this.dragger.addEventListener("mousedown", e => this.dragStart(e));
        this.dragger.addEventListener("mouseup", () => this.dragEnd());

        this.table.addEventListener('scroll', () => {
            this.updateIndicator();
        });

        window.addEventListener('resize', () => {
            clearTimeout(this.timeout);

            this.timeout = setTimeout(() => {
                this.updateWidth();
                this.updateDraggerWidth();
            }, 100);
        });

        // Screen Option fix

        onElementHeightChange(document.getElementById('wpbody-content'), () => {
            this.refreshPosition(300);
        });

    }

    refreshPosition(delay = 100) {

        setTimeout(() => {
            this.show();
            this.updateYPosition();
        }, delay);
    }

    updateIndicator() {
        if (this.active) {
            return;
        }

        let percentage = this.wrapper.getOffsetPercentage() / 100;
        let offset = Math.round(this.maxX * percentage);
        if (offset > this.maxX) {
            offset = this.maxX;
        }

        this.xOffset = offset;
        this.setTranslate(offset);
    }

    getCurrentOffset() {
        return this.xOffset;
    }

    dragStart(e: any) {
        this.initialX = DragHelper.getX(e);
        this.active = true;
    }

    dragEnd() {
        if (!this.active) {
            return;
        }

        this.initialX = 0;
        this.active = false;
        this.xOffset = this.tempX;

        AdminColumns.HorizontalScrolling.wrapper.setOffset(this.percentage);
    }

    drag(e: any) {
        if (this.active) {
            e.preventDefault();

            let movementX = DragHelper.getX(e) - this.initialX;
            let newXpos = this.getCurrentOffset() + movementX;

            if (newXpos < 0) newXpos = 0;
            if (newXpos > this.maxX) newXpos = this.maxX;

            this.tempX = newXpos;
            this.percentage = (newXpos / this.maxX) * 100;

            this.setTranslate(newXpos);
            AdminColumns.HorizontalScrolling.wrapper.setOffset(this.percentage);
        }
    }

    setTranslate(xPos: number) {
        if (xPos === 0) {
            this.element.classList.add('-start')
        } else {
            this.element.classList.remove('-start')
        }
        this.dragger.style.left = `${xPos}px`;
    }

}

const onElementHeightChange = (element: any, callback: Function) => {
    let lastHeight = element.clientHeight, newHeight;
    (function run() {
        newHeight = element.clientHeight;
        if (lastHeight !== newHeight) {
            callback(newHeight)
        }

        lastHeight = newHeight;


        let timer: number = parseInt(element.dataset.onElementHeightChangeTimer);

        if (timer) {
            clearTimeout(parseInt(element.dataset.onElementHeightChangeTimer));
        }

        element.dataset.onElementHeightChangeTimer = setTimeout(run, 200)
    })()
}

class DragHelper {

    static getX(e: any) {
        if (e.type === "touchmove") {
            return e.touches[0].clientX;
        } else {
            return e.clientX;
        }
    }

    static getY(e: any) {
        if (e.type === "touchmove") {
            return e.touches[0].clientY;
        } else {
            return e.clientY;
        }
    }
}