import HorizontalScrolling from "./horizontal-scrolling/main";

declare const jQuery: any;
declare const AdminColumns: any;

jQuery(document).ready(function () {
    let table: HTMLTableElement = document.querySelector('.wp-list-table');

    if (table) {
        let MainModule = new HorizontalScrolling(table);

        jQuery('#acp_overflow_list_screen_table-yes').on('click', function () {

            if (jQuery(this).is(':checked')) {
                MainModule.enable();
                MainModule.store();
            } else {
                MainModule.disable();
                MainModule.store();
            }
        });

        AdminColumns.HorizontalScrolling = MainModule;
    }

});
