import ScrollIndicator from "./indicator";
import TableWrapper from "./table-wrapper";
import ScrollPositionPreference from "./scroll-position-preference";

declare const jQuery: any;
declare const ACP_Horizontal_Scrolling: any;
declare const AC: any;
declare const ajaxurl: any;

const CONST = {
    BODY_CLASS: 'acp-overflow-table'
};

export default class HorizontalScrolling {

    enabled: boolean
    table: HTMLTableElement
    wrapper: TableWrapper
    indicator: ScrollIndicator
    scrollPreference: ScrollPositionPreference

    constructor(table: HTMLTableElement) {
        this.enabled = document.body.classList.contains(CONST.BODY_CLASS);
        this.table = table;
        this.wrapper = new TableWrapper(this.table);
        this.indicator = new ScrollIndicator(this.table, this.wrapper);
        this.scrollPreference = new ScrollPositionPreference(this);

        if (this.isEnabled()) {
            this.wrapper.wrap();
            this.wrapper.enable();

            if (ACP_Horizontal_Scrolling.hasOwnProperty('indicator_enabled') && ACP_Horizontal_Scrolling.indicator_enabled) {
                this.indicator.enable();
            }
        }
    }

    isEnabled() {
        return this.enabled;
    }

    enable() {
        document.body.classList.add(CONST.BODY_CLASS);
        this.enabled = true;
        this.wrapper.wrap();
        this.wrapper.enable();
        this.wrapper.checkWrapperState();
        this.indicator.enable();
    }

    disable() {
        this.wrapper.disable();
        this.enabled = false;
        this.indicator.disable();
        document.body.classList.remove(CONST.BODY_CLASS);
    }

    getScrollLeft() {
        return this.table.scrollLeft;
    }

    setScrollLeft(offset: number) {
        this.table.scrollLeft = offset;
    }

    store() {
        jQuery.post(ajaxurl, {
            action: 'acp_update_table_option_overflow',
            value: this.isEnabled(),
            layout: AC.layout,
            list_screen: AC.list_screen,
            _ajax_nonce: AC.ajax_nonce
        });
    }
}

