// @ts-ignore
import Cookies from 'js-cookie';
import HorizontalScrolling from "./main";

declare const AC: any;

export default class ScrollPositionPreference {

    HorizontalScrolling: HorizontalScrolling;
    timeout: number;
    scrolling: boolean
    offset: number;

    constructor(HorizontalScrolling: HorizontalScrolling) {
        this.HorizontalScrolling = HorizontalScrolling;
        this.timeout = 0
        this.scrolling = false;
        this.offset = 0;
        this.init();
        this.initPosition();
    }

    init() {
        window.addEventListener('beforeunload', () => this.storeCurrentPosition());
    }

    getCacheKey() {
        return `hsoffset_${AC.list_screen}_${AC.layout}`;
    }

    storeCurrentPosition() {
        const inFiveSeconds = new Date(new Date().getTime() + 5 * 1000);

        Cookies.set(this.getCacheKey(), this.HorizontalScrolling.getScrollLeft(), {
            expires: inFiveSeconds
        });
    }

    initPosition() {
        let scrollLeft = Cookies.get(this.getCacheKey());

        if (scrollLeft) {
            setTimeout(() => this.HorizontalScrolling.setScrollLeft(scrollLeft), 100);
        }
    }

}
