const CONST = {
    WRAPPER_CLASS: 'acp-hts-wrapper',
    OVERFLOW_CLASS: '-overflow',
    MORE_CLASS: '-more',
    LESS_CLASS: '-less'
};

export default class TableWrapper {

    enabled: boolean;
    table: HTMLTableElement;
    wrapper: HTMLElement;
    timeout: any;

    constructor(table: HTMLTableElement) {
        this.enabled = false;
        this.table = table;
        this.wrapper = null;
        this.initEvents();
    }

    enable() {
        this.enabled = true;
        this.checkWrapperState();
    }

    disable() {
        this.enabled = false;
    }

    initEvents() {
        this.table.addEventListener('scroll', () => {
            this.checkWrapperState();
        });

        window.addEventListener('resize', () => {
            clearTimeout(this.timeout);

            this.timeout = setTimeout(() => {
                this.checkWrapperState();
            }, 100)
        });

    }

    wrap() {
        if (this.table.parentElement.classList.contains(CONST.WRAPPER_CLASS)) {
            return;
        }

        this.wrapper = document.createElement('div');
        this.wrapper.classList.add(CONST.WRAPPER_CLASS);
        this.table.parentNode.insertBefore(this.wrapper, this.table);
        this.wrapper.appendChild(this.table);
    }

    setOffset(percentage: number) {
        let scrollableAreaWidth = this.table.scrollWidth - this.table.offsetWidth;
        let offsetX = (scrollableAreaWidth * (percentage / 100));

        this.table.scrollLeft = Math.round(offsetX);
    }

    getOffsetPercentage() {
        let scrollableAreaWidth = this.table.scrollWidth - this.table.offsetWidth;
        let offset = this.table.scrollLeft;
        let percentage = (offset / scrollableAreaWidth) * 100;

        return Math.round(percentage);
    }

    checkWrapperState() {
        if (!this.enabled) {
            return;
        }

        let total = this.table.scrollWidth;
        let width = this.table.offsetWidth;
        let scrollLeft = this.table.scrollLeft;

        this.wrapper.classList.remove(CONST.LESS_CLASS, CONST.MORE_CLASS, CONST.OVERFLOW_CLASS);

        if (total > width) {
            this.wrapper.classList.add(CONST.OVERFLOW_CLASS);

            if (scrollLeft + width + 10 < total) {
                this.wrapper.classList.add(CONST.MORE_CLASS);
            }

            if (scrollLeft > 0) {
                this.wrapper.classList.add(CONST.LESS_CLASS);
            }
        }
    }

}